import VueRouter from 'vue-router'
import static_routes from './static_routes'// 导入静态路由
import router from './index'// router实例对象

// 重置路由 -- 新router覆盖旧router
let reset_router = () => {
    let new_router = new VueRouter({
        routes: static_routes,
    })
    router.matcher = new_router.matcher;
}

export default reset_router;