import { Role } from "@/api";

// 根据角色id，获取路由权限
const load_route_permission = async (context, payload) => {
    let { status, msg, data } = await Role.get_route(payload);
    if (status) {
        context.commit('LOAD_ROUTE_PERMISSION', data);
    }
    return { status, msg, data }
}

// 根据角色id，获取操作按钮权限
const load_operation_permission = async (context, payload) => {
    let { status, msg, data } = await Role.get_menu(payload);
    if (status) {
        context.commit('LOAD_OPERATION_PERMISSION', data);
    }
    return { status, msg, data }
}

export default {
    load_route_permission,
    load_operation_permission,
}