// 静态路由
const static_routes = [
    {
        path: '/login',
        alias: '/',// 路由别名
        name: 'Login',
        // meta -- 路由元信息
        meta: {
            require_auth: false,// 登录页面不要权限确认
            route_title: '登录',
        },
        component: () => import( /* webpackChunkName: "Login" */ '@/views/Account/Login.vue')
    },
    {
        path: '/Register',
        name: 'Register',
        meta: {
            require_auth: false,
            route_title: '注册',
        },
        component: () => import( /* webpackChunkName: "Register" */ '@/views/Account/Register.vue')
    },
    {
        // 无权限页面
        path: '/unauthorized',
        name: 'Unauthorized',
        meta: {
            require_auth: false,
            route_title: '未授权访问',
        },
        component: () => import( /* webpackChunkName: "Unauthorized" */ '@/views/Unauthorized.vue')
    },
]

export default static_routes
