// 储存列表
const LOAD_LIST = (state, payload) => {
    state.tableData = [...payload];
}

// 添加
const INSERT_OPERATION = (state, payload) => {
    state.tableData.push({ ...payload });
}

// 删除
const REMOVE_OPERATION = (state, payload) => {
    let { index } = payload;
    state.tableData.splice(index, 1);
}

export default {
    LOAD_LIST,
    INSERT_OPERATION,
    REMOVE_OPERATION,
}