import axios from "axios";

// 获取角色列表
let list = (params) => axios.get('/role/list', { params });

// 添加角色
let create = (data) => axios.post('/role', data);

// 编辑角色
// put 请求 ==> 类似 POST 请求
let edit = (id, data) => axios.put(`/role/${id}`, data);

// 删除角色
// delete 请求 ==> 类似GET请求
let remove = (id, params) => axios.delete(`/role/${id}`, { params });

// 根据角色id，获取路由权限
let get_route = (params) => axios.get('/role/route', { params });

// 配置角色--路由权限
let set_route = (data) => axios.post('/role/route', data);

// 根据角色id，获取菜单权限和操作按钮权限
let get_menu = (params) => axios.get('/role/menu', { params });

// 配置角色--菜单权限，操作权限
let set_menu = (data) => axios.post('/role/menu', data);

export default {
    list,
    create,
    edit,
    remove,
    get_route,
    set_route,
    get_menu,
    set_menu,
}