import axios from "axios";

// 获取用户列表
let list = (params) => axios.get('/link/list', { params });

// 创建链接
let create = (data) => axios.post('/link/', data);

// 启用/禁用 友情链接
let usable = (data) => axios.post('/link/usable', data);

// 编辑链接
// put 请求 ==> 类似 POST 请求
let edit = (link_id, data) => axios.put(`/link/${link_id}`, data);

// 删除链接
// delete请求 ==> 类似 GET 请求
let remove = (link_id, params) => axios.delete(`/link/${link_id}`, { params });

export default {
    list,
    create,
    usable,
    edit,
    remove,
}