<template>
	<el-menu :collapse="collapse" unique-opened class="sidebar el-menu-vertical-demo" router background-color="#545c64"
		text-color="#fff" active-text-color="#ffd04b">
		<!-- v-for 和 v-if 不要同时放在同一个标签上，渲染效率低,v-for 的优先级更高 -->
		<template v-for="menu in menus">
			<!-- ? 运算符，防止因menu无children数组而报错 -->
			<el-submenu :index="menu.route_full_path" v-if="menu.children?.length > 0">
				<template slot="title">
					<i :class="`el-icon-${menu.icon_name}`"></i>
					<span>{{ menu.menu_name }}</span>
				</template>
				<el-menu-item :index="children.route_full_path" v-for="children in menu.children">
					<i :class="`el-icon-${children.icon_name}`"></i>
					<span>
						{{ children.menu_name }}
					</span>
				</el-menu-item>
			</el-submenu>
			<el-menu-item :index="menu.route_full_path" v-else>
				<i :class="`el-icon-${menu.icon_name}`"></i>
				<span>
					{{ menu.menu_name }}
				</span>
			</el-menu-item>
		</template>
	</el-menu>
</template>

<script>
import { Role } from '@/api';
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';
export default {
	data() {
		return {
			menus: [],
		}
	},
	computed: {
		...mapState('layout', ['collapse']),
	},
	created() {
		// 角色 id
		let role_id = sessionStorage.role_id;
		this.load_role_menu();
		// 加载当前操作按钮权限
		this.load_operation_permission({ role_id, resource_type: 3 })
	},
	mounted() {
	},
	methods: {
		// 加载action
		// ...mapActions('role', ['']), -- 角色列表也需要放vuex
		...mapActions('route', ['load_list']),
		...mapActions('menu', ['load_menu']),
		...mapActions('operation', ['loadList']),
		...mapActions('role', ['load_operation_permission']),//权限按钮
		// 加载当前角色菜单
		async load_role_menu() {
			let role_id = sessionStorage.role_id;
			let { status, msg, data } = await Role.get_menu({ role_id, resource_type: 2, type: 'tree' })
			if (status) {
				this.menus = data;
				// 查询是否需要初始化state -- 存在系统管理这个菜单的就需要初始化
				let system_menu = data.find((menu) => menu.permission_code === 'system');
				if (system_menu) {
					this.init_permission_state();
				}
			}
		},
		// 初始化store中的权限
		init_permission_state() {
			// 加载角色列表

			// 加载菜单
			this.load_menu();
			// 加载路由
			this.load_list();
			// 加载按钮  
			this.loadList();
		}
	},

}
</script>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
	width: 200px;
	min-height: 400px;
}
</style>
