import axios from "axios";

// 获取文章最新评论列表
let list = (params) => axios.get('/comment/recent', { params });

// 显示/隐藏评论
let visible = (data) => axios.post('/comment/visible', data);

// 回复评论
let reply = (data) => axios.post('/comment/reply', data);

export default {
    list,
    visible,
    reply,
}