import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'// vuex 的实例对象 
import Layout from '@/components/Layout.vue'// Layout 为公共组件
import route from '@/api/modules/route'
import layout from '@/store/modules/layout'
import { Notification } from 'element-ui';// 单独引入弹窗方法
import static_routes from './static_routes'// 导入静态路由
import global from '@/plugins/global' // 按需导入全局变量--控制动态路由请求  

Vue.use(VueRouter) // VueRouter 以插件的形式嵌入到vue中的

// 下面分为 静态路由 与 动态路由(根据登录的角色不同)(权限路由)

// 创建 router 实例对象
const router = new VueRouter({
	routes: static_routes,
})

// 检查路由是否已存在
let checkRouteExist = function (route) {
	// 获取所有路由
	let routes = router.getRoutes();
	// 查询已经存在该路由
	return routes.some((item) => item.name === route.name);
}

// 注：路由独享守卫，写在上面的路由数组内部
// 注：组件独享守卫，写在组件页面内部
// 全局 -- 前置守卫 -- 这块要画图梳理，逻辑很复杂
router.beforeEach(async (to, from, next) => {
	// 查询是否为静态路由 -- 要去的路由地址to.name 是否在静态路由数组中
	let target_route = static_routes.find((route) => route.name === to.name)

	// 查询是否为动态路由 -- 非静态路由 -- 未匹配到静态路由 -- 操作动态路由
	if (!target_route) { // 动态路由
		// 提取token
		let access_token = sessionStorage.access_token;
		// 无token，未登录状态 -- 跳转登录页  
		if (!access_token) {
			// 弹窗提示
			Notification.error({
				title: `错误`,
				message: '禁止访问，请登录账户！',
			});
			// 重定向，query传参--到目标地址
			next({
				path: '/login',
				query: {
					redirect: to.fullPath
				}
			});
			return;
		}
		// 最外层if防止重复请求动态路由
		if (global.is_route_unload) {
			// 有token -- 登录状态 -- 需要角色id，获取其路由权限 
			let role_id = sessionStorage.role_id;
			await store.dispatch('role/load_route_permission', { role_id });
			// 获取 仓库中的getters的配置信息
			let route_tree = store.getters['role/route_tree'];
			// 动态添加路由
			route_tree.forEach((route) => {
				// 先检查路由是否已经注册
				let is_exist = checkRouteExist(route)
				// 已注册
				if (is_exist) {
					return;
				}
				// 未注册 -- 就正常注册
				router.addRoute(route);
			})
			// 最后添加404页面
			router.addRoute(
				{
					// 404 页面必须放在最后
					path: '*',
					name: 'NotFound',
					meta: {
						require_auth: false,
						route_title: '访问页面不存在',
					},
					component: () => import( /* webpackChunkName: "NotFound" */ '@/views/NotFound.vue')
				}
			)
			// 修改路由加载状态
			global.is_route_unload = false;
			// 触发重定向
			return next({ ...to, replace: true });
		}
	}

	// 操作静态路由
	// 判断是否需要登录认证 
	let is_require_auth = target_route?.meta.require_auth;
	if (!is_require_auth) {
		return next();// 静态路由直接放行
	}
	// 静态路由，需要身份认证的页面，项目暂时未用到

	// 判断目标地址是否要登录认证
	let isRequireAuth = to.matched.some((route) => {
		return route.meta.requiresAuth
	})
	// 无需登录认证
	if (!isRequireAuth) {
		next();// 放行
		return;
	}
})

// 全局 -- 解析守卫

// 全局 -- 后置守卫
router.afterEach((to, from) => {
	let { title_name } = to.meta;
	document.title = title_name ? `${title_name}--CMS管理` : 'CMS管理';// 修改标题
})


// export 路由的实例对象
// 在 main.js 中引入
export default router
