import Vue from "vue";
import store from "@/store";

// 权限指令 -- 操作按钮
Vue.directive('permission', {
    // 使用bind -- 下面 el -- 隐藏 el.style.display = 'none';
    // 此处使用inserted -- el 就可删除 -- el.remove 
    inserted(el, binding) {
        let { value } = binding;
        // 获取store中的getters
        let has_operation = store.getters['role/has_operation'];
        let is_has_operation = has_operation(value);// 判断是否有权限
        if (!is_has_operation) {
            el.remove();
        }
    }
})