import axios from "axios";

// 删除菜单
let remove = (id, params) => axios.delete(`/menu/${id}`, { params });
// 更新菜单
let edit = (id, data) => axios.put(`/menu/${id}`, data);
// 添加子级菜单
let create = (data) => axios.post('/menu/', data);
// 获取子级菜单
let sub = (params) => axios.get('/menu/sub', { params });
// 获取所有菜单权限
let list = (params) => axios.get('/menu/all', { params });
// 菜单页面--删除指定id的操作按钮
let remove_operation = (id, params) => axios.delete(`/menu/operation/${id}`, { params });
// 给菜单页面添加新的操作按钮
let create_operation = (data) => axios.post('/menu/operation', data);
// 菜单页面--编辑操作按钮
let edit_operation = (id, data) => axios.put(`/menu/operation/${id}`, data);
// 设置菜单图标
let icon = (id, data) => axios.put(`/menu/icon/${id}`, data);
// 获取所有element图标
let icon_list = (params) => axios.get('/icon/list', { params });

export default {
    list,
    create,
    create_operation,
    edit,
    remove,
    sub,
    icon,
    icon_list,
    remove_operation,
    edit_operation,
}