// 返回树型结构数据的函数
const tree_data = (state, getters) => {
    let cate_1st = state.flat_data.filter((item) => item.parent_id == 0);
    let treeData = convert(cate_1st, state.flat_data);
    return treeData;
}

// 递归函数返回树形结构
function convert(arr, origin) {
    return arr.map((parent) => {
        let children = origin.filter((child) => parent.route_id === child.parent_id);
        if (children.length) {
            return { ...parent, children: convert(children, origin) };
        } else {
            return { ...parent };
        }
    });
}

export default { tree_data }