// 加载菜单列表
const LOAD_MENU = (state, payload) => {
    state.flat_menu = payload;
}
// 加载图标
const ICON_LIST = (state, payload) => {
    state.icon_list = payload;
}
// 更新菜单列表
const UPDATE_MENU = (state, payload) => {
    state.flat_menu.forEach(element => {
        if (element.menu_id === payload[0]) {
            Object.assign(element, payload[1]);
        } else if (element.permission_id === payload[0]) {
            Object.assign(element, payload[1]);
        }
    });
}
// 添加菜单
const CREATE_MENU = (state, payload) => {
    state.flat_menu.push(payload);
}

// 删除菜单
const REMOVE_MENU = (state, payload) => {
    state.flat_menu.forEach((element, index) => {
        if (element.menu_id === payload) {
            state.flat_menu.splice(index, 1);
        } else if (element.permission_id === payload) {
            state.flat_menu.splice(index, 1);
        }
    });
}
export default {
    LOAD_MENU,
    ICON_LIST,
    UPDATE_MENU,
    CREATE_MENU,
    REMOVE_MENU,
}