import axios from "axios";

// 获取标签列表
let list = (params) => axios.get('/tag/list', { params });

// 添加新标签
let create = (data) => axios.post('/tag/', data);

// 编辑标签
// put 请求 ==> 类似 POST 请求
// API 接口地址变了，传参完善 接口地址。
let edit = (tag_id, data) => axios.put(`/tag/${tag_id}`, data);

// 删除标签
// delete 请求 ==> 类似 GET 请求
// 函数名不要使用 delete ，因为delete是一个关键字
let remove = (tag_id, params) => axios.delete(`/tag/${tag_id}`, { params });

export default {
    list,
    create,
    edit,
    remove,
}