import axios from "axios";

// 获取列表
let list = (params) => axios.get('/operation/list', { params });

// 添加
let add = (data) => axios.post('/operation', data);

// 删除
let remove = (operation_id, params) => axios.delete(`/operation/${operation_id}`, { params });

// 编辑
// put 请求 ==> 类似 POST 请求
let edit = (operation_id, data) => axios.put(`/operation/${operation_id}`, data);

export default {
    list,
    remove,
    edit,
    add,
}