// axios配置文件
import axios from 'axios';
// 单独引入弹窗组件
import { Notification } from 'element-ui';
// 导入router的实例对象导入 -- 用于组件外的路由跳转
import router from '@/router';

// baseURL: 自动补全URL域名 -- 注意修改 -- http
axios.defaults.baseURL = process.env.VUE_APP_SERVER;//开发环境 或者 生产环境 的服务器地址 //即 run serve 或者 run build 

// 拦截器，提前处理一些问题数据
// 添加请求拦截器
axios.interceptors.request.use(function (config) {
	// 在发送请求之前做些什么
	// 提取 access_token
	let access_token = sessionStorage.access_token;
	config.headers.Authorization = `Bearer ${access_token}`;
	return config;
}, function (error) {
	// 对请求错误做些什么
	return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(
	function (response) {
		// 2xx 范围内的状态码都会触发该函数。
		// 返回response里面的data
		return response.data;
	},
	function (error) {
		// 超出 2xx 范围的状态码都会触发该函数。
		// 此处响应拦截器，解决了状态码和token过期问题
		// 状态码
		let { status } = error.response;
		// 请求的URL地址
		let { url } = error.config;

		switch (status) {
			case 401:
				let { message } = error.response.data.inner;
				// 因为这不是vue组件，所以要单独引入该弹窗组件，使用官方给出的用法
				Notification.error({
					title: `错误，${status}--${message}`,
					message: '请重新登录',
					position: 'top-right'
				})
				// 重定向 --> 无token到登录页，登录后到本来想去的页面
				let { fullPath } = router.history.current;// 先获取目标地址
				router.push({
					path: '/login',
					query: { redirect: fullPath },
				});
				break;
			case 403:
				Notification.error({
					title: `错误，${status}`,
					message: `访问API接口${url},未授权`,
					position: 'top-right'
				})
				break;
			case 404:
				Notification.error({
					title: `错误，${status}`,
					message: `访问API接口${url},未授权`,
					position: 'top-right'
				})
				break;
			case 500:
				Notification.error({
					title: `错误，${status}`,
					message: `API接口${url},请联系后台程序员`,
					position: 'top-right'
				})
				break;

			default:
				Notification.error({
					title: `错误，${status}`,
					message: `API接口${url},请查看Ajax发送记录`,
					position: 'top-right'
				})
				break;
		}
		// 对响应错误做点什么
		return Promise.reject(error);
	});
