import { Menu } from "@/api";

// 加载菜单
const load_menu = async (context, payload) => {
    let { status, msg, data } = await Menu.list(payload);
    if (status) {
        context.commit('LOAD_MENU', data);
    }
    return { status, msg, data }
}

// 加载图标
const load_icon = async (context, payload) => {
    let { status, msg, data } = await Menu.icon_list(payload);
    if (status) {
        context.commit('ICON_LIST', data);
    }
    return { status, msg, data }
}

// 添加菜单
const create_menu = async (context, payload) => {
    let { status, msg, data } = await Menu.create(payload)
    if (status) {
        context.commit('CREATE_MENU', { ...data, ...payload });
    }
    return { status, msg, data }
}

// 添加操作按钮
const create_operation = async (context, payload) => {
    let { status, msg, data } = await Menu.create_operation(payload)
    if (status) {
        context.commit('CREATE_MENU', { ...data, ...payload });
    }
    return { status, msg, data }
}

// 添加图标
const create_icon = async (context, payload) => {
    let { menu_id, icon_id } = payload;
    let { status, msg } = await Menu.icon(menu_id, { icon_id })
    if (status) {
        context.commit('UPDATE_MENU', [menu_id, { ...payload }]);
    }
    return { status, msg }
}

// 修改菜单
const edit_menu = async (context, payload) => {
    let { status, msg } = await Menu.edit(payload[0], payload[1])
    if (status) {
        context.commit('UPDATE_MENU', payload);
    }
    return { status, msg }
}

// 修改操作按钮
const edit_operation = async (context, payload) => {
    let { status, msg, data } = await Menu.edit_operation(payload[0], payload[1])
    if (status) {
        context.commit('UPDATE_MENU', payload[1]);
    }
    return { status, msg }
}

// 删除菜单 
const remove_menu = async (context, payload) => {
    let { status, msg } = await Menu.remove(payload[0], payload[1])
    if (status) {
        context.commit('REMOVE_MENU', payload);
    }
    return { status, msg }
}

// 删除操作按钮
const remove_operation = async (context, payload) => {
    let { status, msg } = await Menu.remove_operation(payload)
    if (status) {
        console.log(payload);
        context.commit('REMOVE_MENU', payload);
    }
    return { status, msg }
}

export default {
    load_menu,
    load_icon,
    create_menu,
    edit_menu,
    remove_menu,
    create_operation,
    edit_operation,
    remove_operation,
    create_icon,
}