import axios from "axios";

// data是一个对象，内容为所要传输的字段
// 普通写法
// function login(data) {
//     return axios.post('/admin/login', data)
//     // return出来的是一个promis对象
// }

// post写法：直接传 data 对象,  内部写法：data
// get写法：例：传入的是 {params:{pageindex=1,pagesize=6}} ==> {params:query} 附：query对象
// get写法：直接传入 params 对象 , 内部写法：{params}

// 箭头函数写法
// 登录
let login = (data) => axios.post('/admin/login', data);

// 注册
let register = (data) => axios.post('/admin/register', data);

// 获取管理员列表
// function list(query) {
//     return axios.get('/adminst', { params: query })
// }
// function list(params) {
//     return axios.get('/adminst', { params: params })
// }
// 最终简化版
// function list(params) {
//     return axios.get('/adminst', { params })
// }

// 箭头函数写法
// 获取管理员列表
let list = (params) => axios.get('/admin/list', { params });

// 获取管理员个人资料
let info = (params) => axios.get('/admin/info', { params });

// 编辑管理员个人资料
let edit = (data) => axios.post('/admin/info', data);

// 检测用户名是否可用
let check = (data) => axios.post('/admin/check/username', data);

// 删除账户
let remove = (data) => axios.post('/admin/remove', data);

// 修改本账户信息
let account = (data) => axios.post('/admin/account', data);

// 审核
let audit = (data) => axios.post('/admin/usable', data);

// 给外部使用 
export default {
    login,
    register,
    list,
    info,
    edit,
    check,
    remove,
    account,
    audit,
}



