// 加载路由权限
const LOAD_ROUTE_LIMIT = (state, payload) => {
    state.route_limit = payload;
}
// 修改路由权限
const UPDATE_ROUTE_LIMIT = (state, payload) => {
    state.route_limit[payload[2]] = payload[1];
}
// 加载菜单权限
const LOAD_MENU_LIMIT = (state, payload) => {
    state.menu_limit = payload;
}
// 修改菜单权限
const UPDATE_MENU_LIMIT = (state, payload) => {
    state.menu_limit[payload[2]] = payload[1];
}
export default {
    LOAD_ROUTE_LIMIT,
    UPDATE_ROUTE_LIMIT,
    LOAD_MENU_LIMIT,
    UPDATE_MENU_LIMIT,
}