import axios from "axios";

// 获取轮播图列表
let list = (params) => axios.get('/slide/list', { params });

// 编辑指定id的轮播图列表
let edit = (data) => axios.post('/slide/edit', data);

// 删除指定id轮播图
let remove = (data) => axios.post('/slide/remove', data);

// 添加轮播图
let create = (data) => axios.post('/slide/add', data);

export default {
    list,
    edit,
    remove,
    create,
}