import axios from "axios";

// 获取公告列表
let list = (params) => axios.get('/notice/list', { params });

// 置顶公告
let up = (data) => axios.post('/notice/stick', data);

// 删除指定 id 的公告
let remove = (data) => axios.post('/notice/remove', data);

// 发布公告
let release = (data) => axios.post('/notice/release', data);

// 编辑公告
let edit = (data) => axios.post('/notice/edit', data);

// 获取指定id的公告详情
let detail = (params) => axios.get('/notice/detail', { params })

export default {
    list,
    up,
    remove,
    release,
    edit,
    detail,
}