<template>
	<div class="nav-bar">
		<div class="left">
			<img class="logo am-margin-right-sm" src="../assets/img/common/logo.png">
			<i @click="change" class="el-icon-s-fold"></i>
		</div>
		<el-menu mode="horizontal" background-color="#31404e" text-color="#fff" active-text-color="#ffd04b">
			<el-menu-item index="1">站点设置</el-menu-item>
			<el-menu-item index="2">预览网站</el-menu-item>
			<el-submenu index="3">
				<template slot="title">
					<el-avatar :src="profile.avatar"></el-avatar>
					<span class="username am-margin-left-sm">{{ profile.fullname }}</span>
				</template>
				<el-menu-item index="/admin/info">账户设置</el-menu-item>
				<el-menu-item>消息通知</el-menu-item>
				<el-menu-item @click="handleLogout">退出登录</el-menu-item>
			</el-submenu>
		</el-menu>
	</div>
</template>

<script>
import { mapMutations, mapActions, mapState } from 'vuex';
import reset_router from '@/router/reset_router'; // 重置路由

export default {
	data() {
		return {

		}
	},
	computed: {
		...mapState('user', ['profile']),
	},
	created() {
		// 加载当前账户信息
		// 需要参数user_id
		let { user_id } = sessionStorage;
		this.load_profile({ user_id });
	},
	methods: {
		...mapMutations('layout', ['change']),

		...mapActions('user', ['load_profile']),

		// 退出登录
		handleLogout() {
			// 清空sessionStorage一些数据
			sessionStorage.removeItem('access_token')
			sessionStorage.removeItem('refresh_token')
			sessionStorage.removeItem('user_id')
			sessionStorage.removeItem('role_id')
			// 重置路由
			reset_router();
			// 跳转登录
			this.$router.replace('/login');
		},
	}
}
</script>

<style lang="less" >
.nav-bar {
	display: flex;
	justify-content: space-between;
	align-items: center;


	.left {
		display: flex;
		align-items: center;

		img.logo {
			height: 50px;
		}
	}

	.username {
		margin-left: 10px;
	}

	.el-menu.el-menu--horizontal {
		border-bottom: 1px solid rgb(49, 64, 78);
	}
}
</style>
