import { Operation } from "@/api"

// 加载列表
const loadList = async (context, payload) => {
    let { status, data } = await Operation.list({ pagesize: 20 }, payload);
    if (status) {
        context.commit('LOAD_LIST', data);
    }
}

// 添加按钮
const insertOperation = async (context, payload) => {
    let { status, data, msg } = await Operation.add(payload);
    if (status) {
        context.commit("INSERT_OPERATION", { ...data, ...payload });
    }
    return { status, msg }
}

// 编辑
const editOperation = async (context, payload) => {
    let { operation_id } = payload;
    let { status, data, msg } = await Operation.edit(operation_id, { ...payload });
    return { status, msg };
}

// 删除
const removeOperation = async (context, payload) => {
    let { operation_id } = payload;
    let { status, data, msg } = await Operation.remove(operation_id, { ...payload });
    if (status) {
        context.commit("REMOVE_OPERATION", { ...payload });
    }
    return { status, msg };
}

export default {
    loadList,
    insertOperation,
    editOperation,
    removeOperation,
}