// 加载
const UPDATE_ROUTE = (state, payload) => {
    state.flat_data = [...payload];
}

// 添加
const INSERT_ROUTE = (state, payload) => {
    state.flat_data.push(payload);
}

// 刪除
const REMOVE_ROUTE = (state, payload) => {
    state.flat_data.splice(payload, 1);
}

export default {
    UPDATE_ROUTE,
    INSERT_ROUTE,
    REMOVE_ROUTE
}