import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// element UI 配置文件
import './plugins/element.js'
// axios 配置文件
import '@/plugins/axios.js'
// 全局注册指令 -- 按钮权限   注：后期指令多了不可这样直接注册，要封装一波
import '@/directives/permission'
// server服务器的环境变量 -- 挂载到原型上 -- 这样任何组件都可以直接去使用 -- 因为process.env.VUE_APP_SERVER只能放在js文件中
Vue.prototype.$server = process.env.VUE_APP_SERVER;

Vue.config.productionTip = false

new Vue({
	// 在 根组件 中挂载 router 实例对象，子组件中使用 ==> $router
	router,
	store,
	render: h => h(App)
}).$mount('#app')
